"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

interface ClientData {
  name: string
  email: string
  phone: string
  notes: string
  documents: { name: string; url: string }[]
}

export default function ClientPortal() {
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [loginData, setLoginData] = useState({ email: "", password: "" })
  const [clientData, setClientData] = useState<ClientData>({
    name: "Cliente Ejemplo",
    email: "cliente@ejemplo.com",
    phone: "123-456-7890",
    notes: "Notas del cliente",
    documents: [
      { name: "Documento 1.pdf", url: "#" },
      { name: "Documento 2.pdf", url: "#" }
    ]
  })

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault()
    // Aquí iría la lógica de autenticación real
    if (loginData.email && loginData.password) {
      setIsLoggedIn(true)
    }
  }

  if (!isLoggedIn) {
    return (
      <div className="min-h-screen bg-white p-8">
        <div className="max-w-md mx-auto">
          <Card className="p-6">
            <h1 className="text-2xl font-bold mb-6 text-center">Portal del Cliente</h1>
            <form onSubmit={handleLogin} className="space-y-4">
              <div>
                <Label htmlFor="email">Email</Label>
                <Input
                  id="email"
                  type="email"
                  value={loginData.email}
                  onChange={(e) => setLoginData({ ...loginData, email: e.target.value })}
                  placeholder="email@ejemplo.com"
                  required
                />
              </div>

              <div>
                <Label htmlFor="password">Contraseña</Label>
                <Input
                  id="password"
                  type="password"
                  value={loginData.password}
                  onChange={(e) => setLoginData({ ...loginData, password: e.target.value })}
                  required
                />
              </div>

              <Button type="submit" className="w-full">
                Iniciar Sesión
              </Button>
            </form>
          </Card>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-white p-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-4xl font-bold">Mi Portal</h1>
          <Button variant="outline" onClick={() => setIsLoggedIn(false)}>
            Cerrar Sesión
          </Button>
        </div>

        <div className="grid gap-6">
          <Card className="p-6">
            <h2 className="text-2xl font-semibold mb-4">Información Personal</h2>
            <div className="space-y-2">
              <p><span className="font-semibold">Nombre:</span> {clientData.name}</p>
              <p><span className="font-semibold">Email:</span> {clientData.email}</p>
              <p><span className="font-semibold">Teléfono:</span> {clientData.phone}</p>
              {clientData.notes && (
                <p><span className="font-semibold">Notas:</span> {clientData.notes}</p>
              )}
            </div>
          </Card>

          <Card className="p-6">
            <h2 className="text-2xl font-semibold mb-4">Mis Documentos</h2>
            <div className="space-y-2">
              {clientData.documents.map((doc, index) => (
                <div key={index} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                  <span>{doc.name}</span>
                  <Button variant="outline" size="sm" asChild>
                    <a href={doc.url} download>
                      Descargar
                    </a>
                  </Button>
                </div>
              ))}
            </div>
          </Card>
        </div>
      </div>
    </div>
  )
}
